/* global angular */
import stateContainerModuleKey, { bbStateContainerKey } from 'lib-bb-state-container-ng';
import Router from './router';

const moduleKey = 'ui-wm-router-module';

export const routerKey = `${moduleKey}:router`;

export default angular
  .module(moduleKey, [stateContainerModuleKey])
  .factory(routerKey, [
    bbStateContainerKey,
    Router,
  ])
  .name;



